.login-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #ffffff 0%, #f5f5f5 100%);
  padding: 20px;
}

.login-box {
  background: #ffffff;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  padding: 40px;
  width: 100%;
  max-width: 450px;
}

.login-title {
  font-size: 32px;
  font-weight: 700;
  color: #000000;
  margin-bottom: 8px;
  text-align: center;
}

.login-subtitle {
  font-size: 16px;
  color: #333333;
  margin-bottom: 32px;
  text-align: center;
}

.error-message {
  background-color: #fee;
  color: #c33;
  padding: 12px 16px;
  border-radius: 8px;
  font-size: 14px;
  margin-bottom: 20px;
  border: 1px solid #fcc;
  font-family: 'Poppins', sans-serif;
}

.login-form {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.form-group label {
  font-size: 14px;
  font-weight: 500;
  color: #000000;
}

.form-group input {
  padding: 12px 16px;
  border: 2px solid #e0e0e0;
  border-radius: 8px;
  font-size: 16px;
  font-family: 'Poppins', sans-serif;
  color: #000000;
  transition: border-color 0.3s ease;
  outline: none;
}

.form-group input:focus {
  border-color: #FF6B35;
}

.form-group input::placeholder {
  color: #999999;
}

.password-input-wrapper {
  position: relative;
}

.password-input-wrapper input {
  width: 100%;
  padding-right: 45px;
}

.password-toggle {
  position: absolute;
  right: 12px;
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  cursor: pointer;
  color: #666666;
  font-size: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 4px;
  transition: color 0.3s ease;
}

.password-toggle:hover {
  color: #FF6B35;
}

.login-button {
  padding: 14px;
  background-color: #FF6B35;
  color: #ffffff;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 600;
  font-family: 'Poppins', sans-serif;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  margin-top: 8px;
}

.login-button:hover {
  background-color: #e55a2b;
  transform: translateY(-2px);
}

.login-button:active {
  transform: translateY(0);
}

.login-button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
  transform: none;
}

.login-button:disabled:hover {
  background-color: #ccc;
  transform: none;
}

.form-group input:disabled {
  background-color: #f5f5f5;
  cursor: not-allowed;
}

.signup-link {
  text-align: center;
  margin-top: 24px;
  font-size: 14px;
  color: #333333;
}

.create-link {
  color: #FF6B35;
  font-weight: 600;
  cursor: pointer;
  text-decoration: none;
  transition: color 0.3s ease;
}

.create-link:hover {
  color: #e55a2b;
  text-decoration: underline;
}

@media (max-width: 480px) {
  .login-container {
    padding: 12px;
    align-items: flex-start;
    padding-top: 24px;
  }

  .login-box {
    padding: 24px 20px;
  }

  .login-title {
    font-size: 24px;
  }

  .login-subtitle {
    font-size: 14px;
    margin-bottom: 24px;
  }

  .login-form {
    gap: 18px;
  }

  .form-group input {
    padding: 14px 16px;
    font-size: 16px;
  }

  .login-button {
    padding: 14px;
    min-height: 48px;
  }
}
