.sidebar {
  width: 250px;
  min-width: 250px;
  background-color: #ffffff;
  border-right: 1px solid #e0e0e0;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  box-shadow: 2px 0 4px rgba(0, 0, 0, 0.05);
  transition: transform 0.25s ease, box-shadow 0.25s ease;
}

/* Mobile: overlay drawer - smooth slide animation */
@media (max-width: 991px) {
  .sidebar {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: 999;
    transform: translateX(-100%);
    max-width: min(280px, 85vw);
    width: 280px;
    min-width: unset;
    box-shadow: none;
    transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  }

  .sidebar.sidebar--open {
    transform: translateX(0);
    box-shadow: 4px 0 20px rgba(0, 0, 0, 0.15);
  }

  .sidebar-close-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 12px;
    right: 12px;
    width: 44px;
    height: 44px;
    padding: 0;
    border: none;
    background: #f5f5f5;
    color: #666;
    border-radius: 8px;
    cursor: pointer;
    font-size: 20px;
    transition: background 0.2s, color 0.2s;
    z-index: 2;
  }

  .sidebar-close-btn:hover {
    background: #e0e0e0;
    color: #333;
  }

  .sidebar-close-btn:focus-visible {
    outline: 2px solid #FF6B35;
    outline-offset: 2px;
  }
}

@media (min-width: 992px) {
  .sidebar-close-btn {
    display: none;
  }
}

.sidebar-nav {
  display: flex;
  flex-direction: column;
  padding: 20px 0;
  padding-top: 56px;
  gap: 8px;
  flex: 1;
  min-height: 0;
  overflow-y: auto;
  overflow-x: hidden;
}

@media (min-width: 992px) {
  .sidebar-nav {
    padding-top: 20px;
  }
}

.nav-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 14px 20px;
  color: #666666;
  text-decoration: none;
  transition: all 0.3s ease;
  margin: 0 10px;
  border-radius: 8px;
  font-family: 'Poppins', sans-serif;
  font-size: 15px;
  font-weight: 500;
}

.nav-item:hover {
  background-color: #f5f5f5;
  color: #333333;
  transform: translateX(4px);
}


.nav-item.active {
  background-color: #cfcfcf;
  color: #585858;
  font-weight: 700;
  box-shadow: 0 2px 8px rgba(255, 107, 53, 0.08);
  border-radius: 10px;
  transition: background 0.2s, color 0.2s;
}

.nav-item.active:hover {

  color: #fffefd;
  transform: translateX(1px);
}

.nav-icon {
  font-size: 18px;
  flex-shrink: 0;
}

.nav-label {
  flex: 1;
}

/* Settings Dropdown */
.settings-menu-wrapper {
  position: relative;
}

.settings-main {
  position: relative;
}

.dropdown-icon {
  font-size: 12px;
  margin-left: auto;
  transition: transform 0.3s ease;
}

.settings-submenu {
  margin-left: 20px;
  margin-top: 4px;
  padding-left: 20px;
  border-left: 2px solid #e0e0e0;
  animation: slideDown 0.3s ease;
}

@keyframes slideDown {
  from {
    opacity: 0;
    max-height: 0;
  }
  to {
    opacity: 1;
    max-height: 500px;
  }
}

.submenu-item {
  padding-left: 40px !important;
  margin-left: 0 !important;
  position: relative;
}

.submenu-icon {
  font-size: 16px;
  margin-left: 8px;
}

.submenu-item.active {
  background-color: #FFF5F2 !important;
  color: #FF6B35;
  font-weight: 600;
}

.submenu-item.active:hover {
  background-color: #FFF5F2 !important;
}

.submenu-indicator {
  position: absolute;
  left: 20px;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background-color: #999999;
  transition: all 0.3s ease;
}

.submenu-item.active .submenu-indicator {
  background-color: #FF6B35;
  width: 8px;
  height: 8px;
}

.submenu-item:hover .submenu-indicator {
  background-color: #FF6B35;
}

/* User Profile Section */
.sidebar-user-section {
  padding: 16px 20px;
  border-bottom: 1px solid #e0e0e0;
  margin-bottom: 8px;
}

.sidebar-user-profile {
  display: flex;
  align-items: center;
  gap: 12px;
}

.sidebar-profile-circle {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background: #FF6B35;
  color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  font-size: 18px;
  font-family: 'Poppins', sans-serif;
  flex-shrink: 0;
}

.sidebar-user-info {
  flex: 1;
  min-width: 0;
}

.sidebar-user-name {
  font-size: 16px;
  font-weight: 600;
  color: #111b21;
  font-family: 'Poppins', sans-serif;
  margin-bottom: 2px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.sidebar-user-email {
  font-size: 13px;
  color: #667781;
  font-family: 'Poppins', sans-serif;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/* Logout Button */
.sidebar-logout-btn {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 14px 20px;
  color: #dc3545;
  text-decoration: none;
  transition: all 0.3s ease;
  margin: 8px 10px 20px 10px;
  border-radius: 8px;
  font-family: 'Poppins', sans-serif;
  font-size: 15px;
  font-weight: 500;
  border: none;
  background: transparent;
  cursor: pointer;
  width: calc(100% - 20px);
}

.sidebar-logout-btn:hover {
  background-color: #fee;
  color: #c82333;
  transform: translateX(4px);
}

.sidebar-logout-btn .nav-icon {
  color: inherit;
}